.footer {
	grid-column: 1 / 3;
	background: #191a1d;
	margin: 200px 0 0 0;
	padding: 50px 0;
	display: flex;
	align-items: center;
	justify-content: center;

	.author {
		color: rgb(129, 129, 129);

		cursor: pointer;
		margin: 0;

		&:hover {
			color: #fff;
			background: rgba(226, 226, 226, 0.47);
			box-shadow: 0 0 30px 5px white;
		}
	}
}
